html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: "Aesthetic";
  src: url("../fonts/Aesthetic-Regular.woff2") format("woff2"), url("../fonts/Aesthetic-Regular.woff") format("woff"), url("../fonts/Aesthetic-Regular.ttf") format("truetype");
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "CrimsonText-SemiBold";
  src: url("../fonts/CrimsonText-SemiBold.ttf") format("truetype");
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "WorkSans-Regular";
  src: url("../fonts/WorkSans-Regular.ttf") format("truetype");
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "WorkSans-SemiBold";
  src: url("../fonts/WorkSans-SemiBold.ttf") format("truetype");
  font-style: normal;
  font-display: swap;
}
.h1, .h2 {
  color: #FFC961;
  font-family: "Aesthetic", sans-serif;
}

.work-sans {
  font-family: "WorkSans-Regular";
}

.work-sans-semibold {
  font-family: "WorkSans-SemiBold";
}

.aesthetic {
  font-family: "Aesthetic", sans-serif;
}

.crimson-semibold {
  font-family: "CrimsonText-SemiBold";
}

.h1 {
  font-size: 150px;
}
