.centered {
  text-align: center;
}

.button {
  display: block;
  padding: 15px 5px;
  width: 100%;
  max-width: 350px;
  border-radius: 62px;
}

a, button {
  color: inherit;
  text-decoration: inherit;
  border: 0;
}

button {
  cursor: pointer;
}

svg {
  max-width: 100%;
}

.horarios, .datos {
  letter-spacing: 2px;
  font-size: 26px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
@media (max-width: 724px) {
  .horarios, .datos {
    display: block;
    font-size: 22px;
  }
}

.intro {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  max-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  overflow: hidden;
}
@media (max-width: 724px) {
  .intro {
    display: block;
    flex-direction: unset;
  }
}
.intro img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: absolute;
}
.intro .main-title {
  color: #FFE0A5;
  position: relative;
  align-self: flex-end;
  text-align: center;
  margin: 0 auto 5vh;
  padding: 0 35px;
}
@media (max-width: 724px) {
  .intro .main-title {
    padding: 0 15px;
    align-self: unset;
  }
}
.intro .sparks {
  max-width: 80%;
  margin: 0 auto 10vh;
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
}
@media (max-width: 724px) {
  .intro .sparks {
    height: 50%;
  }
}
@media (max-width: 640px) {
  .intro .sparks-l {
    margin-left: -25vw;
  }
}
.intro .sparks-r {
  margin-left: auto;
}
@media (max-width: 640px) {
  .intro .sparks-r {
    margin-right: -20vw;
  }
}
@media (max-width: 724px) {
  .intro .datos {
    margin: 1rem auto;
    justify-content: center;
  }
}
.intro .datos span {
  width: 300px;
  height: 1px;
  background-color: #FFE0A5;
  display: block;
  margin: 0 2rem;
}
@media (max-width: 724px) {
  .intro .datos span {
    margin: 1rem auto;
  }
}

.invited {
  padding: 150px 35px 85px;
  position: relative;
}
@media (max-width: 640px) {
  .invited {
    padding: 90px 35px 60px;
  }
}
.invited svg {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 5%;
}
@media (max-width: 640px) {
  .invited svg {
    max-width: 95%;
  }
}
.invited .h2 {
  margin-bottom: 35px;
}
.invited .work-sans {
  font-size: 24px;
}
@media (max-width: 640px) {
  .invited .work-sans {
    font-size: 18px;
    line-height: 1.5rem;
  }
}
.invited .counter {
  color: #0C3044;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 153px auto;
  width: 1024px;
  max-width: 100%;
}
@media (max-width: 640px) {
  .invited .counter {
    margin: 85px auto 40px;
  }
}
.invited .counter .num {
  font-size: 128px;
  margin-bottom: 65px;
}
@media (max-width: 640px) {
  .invited .counter .num {
    font-size: 65px;
    margin-bottom: 20px;
  }
}

.fecha {
  padding: 150px 35px 150px;
  background-color: #0C2736;
  color: #fff;
}
@media (max-width: 640px) {
  .fecha {
    padding: 80px 35px;
  }
}
@media (max-width: 640px) {
  .fecha h2.h2 {
    font-size: 35px;
  }
}
.fecha .itinerario {
  margin-top: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 780px) {
  .fecha .itinerario {
    display: block;
    margin-top: 35px;
  }
  .fecha .itinerario svg {
    max-width: 60px;
  }
}
.fecha .itinerario svg {
  margin-right: 5vw;
}
.fecha .horarios {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 800px;
}
.fecha .horarios .horario {
  font-size: 24px;
  display: flex;
  width: 100%;
  align-items: center;
  height: 80px;
  position: relative;
}
@media (max-width: 640px) {
  .fecha .horarios .horario {
    font-size: 18px;
  }
}
.fecha .horarios .horario .work-sans {
  font-size: 14px;
  position: absolute;
  left: 45px;
  top: 85px;
  letter-spacing: 0;
}
.fecha .horarios .horario.rob {
  height: 100px;
}
.fecha .horarios .horario span {
  width: 300px;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.37);
  display: block;
  margin: 0 2rem;
}
@media (max-width: 640px) {
  .fecha .horarios .horario span {
    width: 80px;
  }
}

.map {
  height: 600px;
  width: 100%;
}
.map iframe {
  height: 600px;
  width: 100%;
}

.fotos {
  display: grid;
  height: 100%;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto auto auto;
}
.fotos div img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
}
.fotos div.two {
  grid-row-start: 2;
}
.fotos div.three {
  grid-column-start: span 2;
  grid-row-end: span 2;
}

.mesa {
  background-color: #0C2736;
  padding: 150px 35px 150px;
}
@media (max-width: 640px) {
  .mesa {
    padding: 80px 15px 0;
  }
  .mesa .h2 {
    font-size: 35px;
  }
}
.mesa .button-star {
  margin-top: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 640px) {
  .mesa .button-star {
    margin-top: 35px;
  }
}
.mesa .button-star .button {
  background-color: #fff;
  display: block;
  margin: 0 25px;
  font-size: 20px;
  color: #0C3044;
}
@media (max-width: 640px) {
  .mesa .button-star .button {
    font-size: 14px;
  }
}

.form {
  background-color: #0C2736;
  padding: 150px 35px 150px;
  background: rgb(12, 47, 65);
  background: linear-gradient(180deg, #0c2736 20%, rgb(31, 119, 166) 60%);
}
@media (max-width: 640px) {
  .form {
    padding: 80px 15px;
  }
  .form .h2 {
    font-size: 35px;
  }
}
.form .wrapper {
  width: 100%;
  max-width: 800px;
  margin: 0 auto 0;
  position: relative;
}
@media (max-width: 640px) {
  .form .wrapper {
    width: 90%;
  }
}
.form .wrapper .bottom, .form .wrapper .top {
  position: absolute;
}
.form .wrapper .bottom svg, .form .wrapper .top svg {
  width: 70px !important;
}
@media (max-width: 640px) {
  .form .wrapper .bottom svg, .form .wrapper .top svg {
    width: 30px !important;
  }
}
.form .wrapper .top {
  right: -30px;
  top: -20px;
}
.form .wrapper .bottom {
  left: -30px;
  bottom: -20px;
}
.form .container {
  width: 100%;
  margin: 150px auto 0;
  max-width: 600px;
  background-color: #fff;
  padding: 50px 35px;
  border-radius: 158px;
  min-height: 600px;
  box-sizing: border-box;
}
@media (max-width: 640px) {
  .form .container {
    margin: 35px auto;
    border-radius: 80px;
  }
  .form .container .h2 {
    font-size: 35px;
  }
}
.form .button {
  background-color: #0C3044;
  display: block;
  margin: 0 25px;
  font-size: 14px;
  color: #fff;
  margin: 0 auto;
}
.form .bye-svg {
  width: 100%;
  height: auto;
}
.form .bye-svg svg {
  max-width: 100%;
}

.fade-in-image {
  animation: fadeIn 5s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}